 body{
    margin: 0%;
    padding: 0%;
  }

 a{
    text-decoration: none !important;
 }

 .transparency{
   background-color: rgba(0, 0, 0, 0.5);
 }

 .cssbuttons-io-button {
   background: #A370F0;
   color: white;
   font-family: inherit;
   padding: 0.35em;
   padding-left: 1.2em;
   font-size: 17px;
   font-weight: 500;
   border-radius: 0.9em;
   border: none;
   letter-spacing: 0.05em;
   display: flex;
   align-items: center;
   box-shadow: inset 0 0 1.6em -0.6em #714da6;
   overflow: hidden;
   position: relative;
   height: 2.8em;
   padding-right: 3.3em;
 }
 
 .cssbuttons-io-button .icon {
   background: white;
   margin-left: 1em;
   position: absolute;
   display: flex;
   align-items: center;
   justify-content: center;
   height: 2.2em;
   width: 2.2em;
   border-radius: 0.7em;
   box-shadow: 0.1em 0.1em 0.6em 0.2em #7b52b9;
   right: 0.3em;
   transition: all 0.3s;
 }
 
 .cssbuttons-io-button:hover .icon {
   width: calc(100% - 0.6em);
 }
 
 .cssbuttons-io-button .icon svg {
   width: 1.1em;
   transition: transform 0.3s;
   color: #7b52b9;
 }
 
 .cssbuttons-io-button:hover .icon svg {
   transform: translateX(0.1em);
 }
 
 .cssbuttons-io-button:active .icon {
   transform: scale(0.95);
 }
 

 .love-heart:before,#switch {
   display: none;
  }
  
  .love-heart, .love-heart::after {
   border-color: #DEE1ED;
   border: 1px solid;
   border-top-left-radius: 100px;
   border-top-right-radius: 100px;
   width: 10px;
   height: 8px;
   border-bottom: 0
  }
  
  .round {
   position: absolute;
   z-index: 1;
   width: 8px;
   height: 8px;
   background: #fff;
   box-shadow: rgba(0, 0, 0, 0.24) 0px 0px 4px 0px;
   border-radius: 100%;
   left: 0px;
   bottom: -1px;
   transition: all .5s ease;
   animation: check-animation2 .5s forwards;
  }
  
  input:checked + label .round {
   transform: translate(0px, 0px);
   animation: check-animation .5s forwards;
   background-color: #C1D9BF
  }
  
  @keyframes check-animation {
   0% {
    transform: translate(0px, 0px);
   }
  
   50% {
    transform: translate(0px, 7px);
   }
  
   100% {
    transform: translate(7px, 7px);
   }
  }
  
  @keyframes check-animation2 {
   0% {
    transform: translate(7px, 7px);
   }
  
   50% {
    transform: translate(0px, 7px);
   }
  
   100% {
    transform: translate(0px, 0px);
   }
  }


  label {
   /* Presentation */
   font-size: 48px
 }
 
 /* Required Styling */
 
 label input[type="checkbox"] {
   display: none;
 }
 
 .custom-checkbox {
   margin-left: 2em;
   position: relative;
   cursor: pointer;
 }
 
 .custom-checkbox .glyphicon {
   color: gold;
   position: absolute;
   top: 0.4em;
   left: -1.25em;
   font-size: 0.75em;
 }
 
 .custom-checkbox .glyphicon-star-empty {
   color: gray;
 }
 
 .custom-checkbox .glyphicon-star {
   opacity: 0;
   transition: opacity 0.2s ease-in-out;
 }
 
 .custom-checkbox:hover .glyphicon-star{
   opacity: 0.5;
 }
 
 .custom-checkbox input[type="checkbox"]:checked ~ .glyphicon-star {
   opacity: 1;
 }


 /* Make the tag position relative to the figure */
.figure.tag {
   position: relative;
 }
 /* set the base styles all tags should use */
 .figure.tag::before {
   position: absolute;
   top: 10%;
   display: block;
   color: white;
   padding: 0.5rem 1rem;
   font-weight: bold;
 }
 /* Specific variations for different tags */
 .figure.tag-under-construction::before {
   content: "Under Construction";
   background: orange;
 }

 .figure.tag-ready-to-move::before {
   content: "Ready To Move";
   background: rgb(149, 119, 63);
 }
 
 .figure.tag-upcoming::before {
   content: "Upcoming";
   background: rgb(217, 206, 184);
 }
 
 .figure.tag-completed::before {
   content: "Completed";
   background: rgb(26, 201, 49);
 }

 .figure.tag-just-lauched::before {
   content: "Just Launched";
   background: rgb(120, 138, 204);
 }

  .figure.tag-on-sale::before {
    content: "On Sale";
    background: rgb(120, 138, 204);
  }
 
 
 .figure.tag-out::before {
   content: "Out of Stock";
   background: #e2e2e2;
   border: #ccc;
   color: #444;
   top: 42%;
   right: 0;
   left: 0;
   width: 30%;
   margin: 0 auto;
   text-align: center;
 }
 

 .contact-form-wrapper {
  padding: 0px;
}

.contact-form {
  padding: 10px 10px;
  background-color: #ffffff;
  border-radius: 12px;
  max-width: 400px;
}

.contact-form textarea {
  resize: none;
}

.contact-form .form-input,
.form-text-area {
  background-color: #f0f4f5;
  height: 50px;
  padding-left: 16px;
}

.contact-form .form-text-area {
  background-color: #f0f4f5;
  height: auto;
  padding-left: 16px;
}

.contact-form .form-control::placeholder {
  color: #aeb4b9;
  font-weight: 500;
  opacity: 1;
}

.contact-form .form-control:-ms-input-placeholder {
  color: #aeb4b9;
  font-weight: 500;
}

.contact-form .form-control::-ms-input-placeholder {
  color: #aeb4b9;
  font-weight: 500;
}

.contact-form .form-control:focus {
  border-color: #f33fb0;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.07), 0 0 8px #f33fb0;
}

.contact-form .title {
  text-align: center;
  font-size: 24px;
  font-weight: 500;
}

.contact-form .description {
  color: #aeb4b9;
  font-size: 14px;
  text-align: center;
}

.contact-form .submit-button-wrapper {
  text-align: center;
}

.contact-form .submit-button-wrapper input {
  border: none;
  border-radius: 4px;
  background-color: #f23292;
  color: white;
  text-transform: uppercase;
  padding: 10px 60px;
  font-weight: 500;
  letter-spacing: 2px;
}

.contact-form .submit-button-wrapper input:hover {
  background-color: #d30069;
}

